import 'popper.js'
import moment from 'moment'
import 'bootstrap/dist/js/bootstrap.js'

function includeHTML() {
    const z = document.getElementsByTagName('*')
    let elmnt
    let file
    let xhttp
    /* Loop through a collection of all HTML elements: */
    for (let i = 0; i < z.length; i++) {
        elmnt = z[i]
        /* search for elements with a certain atrribute: */
        file = elmnt.getAttribute('include-html')
        if (file) {
            /* Make an HTTP request using the attribute value as the file name: */
            xhttp = new XMLHttpRequest()
            xhttp.onreadystatechange = function() {
                if (this.readyState == 4) {
                    if (this.status == 200) {
                        elmnt.innerHTML = this.responseText

                        $(document).ready(function() {
                            //header start
                            $(document).on('click', function(e) {
                                if (
                                    $(e.target).closest('.menu-wrapper')
                                        .length === 0
                                ) {
                                    $('.menu-wrapper .nav-link').removeClass(
                                        'active'
                                    )
                                    $('.menu-wrapper .tab-pane')
                                        .removeClass('active')
                                        .removeClass('show')
                                }
                            })

                            var isAdOpen = false
                            $('#ad-button').click(function() {
                                if (!isAdOpen) {
                                    $('#ad-button-text').text('關閉視窗')
                                    $('#ad-image-open').show()
                                    $('#ad-open-icon')
                                        .addClass('fa-times')
                                        .removeClass('fa-chevron-down')
                                } else {
                                    $('#ad-button-text').text('展開看看')
                                    $('#ad-image-open').hide()
                                    $('#ad-open-icon')
                                        .addClass('fa-chevron-down')
                                        .removeClass('fa-times')
                                }
                                isAdOpen = !isAdOpen
                            })
                            $('#m-banner-close-button').click(function() {
                                $('#m-banner').hide()
                                $('.search-page').css('padding-top', '56px')
                            })

                            var isMenuSearchShow = false
                            $('#menu-search').click(function() {
                                if (isMenuSearchShow) {
                                    $(
                                        '.mobile .nav-bar,#sarch-overlay'
                                    ).addClass('d-none')
                                } else {
                                    $(
                                        '.mobile .nav-bar,#sarch-overlay'
                                    ).removeClass('d-none')
                                }
                                isMenuSearchShow = !isMenuSearchShow
                            })
                            //header end

                            //search start
                            var isAdvanceSearchShow = false
                            $('#btn-advance-search').click(function() {
                                if (isAdvanceSearchShow) {
                                    $('.fa-minus,#div-advance-search').addClass(
                                        'd-none'
                                    )
                                    $('.fa-plus').removeClass('d-none')
                                    $(this).css('background-color', '#efefef')
                                } else {
                                    $(
                                        '.fa-minus,#div-advance-search'
                                    ).removeClass('d-none')
                                    $('.fa-plus').addClass('d-none')
                                    $(this).css('background-color', '#fff9e9')
                                }
                                isAdvanceSearchShow = !isAdvanceSearchShow
                            })
                            //search end

                            //forgot password
                            $('.input-password-icon').click(function() {
                                $(this).toggleClass('fa-eye-slash fa-eye')
                                var input = $('#password')
                                if (input.attr('type') == 'password') {
                                    input.attr('type', 'text')
                                } else {
                                    input.attr('type', 'password')
                                }
                            })

                            //forgot passwordend

                            // //school family
                            var tempObject
                            $('.reply-icon').click(function() {
                                $('.blueBG').addClass('d-none')
                                $(this).addClass('d-none')
                                $(this)
                                    .parents('.blueBG')
                                    .removeClass('d-none')
                                $(this)
                                    .parents('.blueBG')
                                    .find('.upload-section')
                                    .removeClass('d-none')
                                $('.submit-section').removeClass('d-none')
                                tempObject = $(this)
                            })

                            $('.submit-section .submit').click(function() {
                                //TODO write tour upload function here
                            })

                            $('.submit-section .cancel').click(function() {
                                tempObject.removeClass('d-none')
                                $('.blueBG').removeClass('d-none')
                                $('.upload-section').addClass('d-none')
                                $('.submit-section').addClass('d-none')
                            })
                            // //school family end
                        })
                    }
                    if (this.status == 404) {
                        elmnt.innerHTML = 'Page not found.'
                    }
                    /* Remove the attribute, and call this function once more: */
                    elmnt.removeAttribute('include-html')
                    includeHTML()
                }
            }
            xhttp.open('GET', file, true)
            xhttp.send()
            /* Exit the function: */
            return
        }
    }
}

function autoImportHeaderImg() {
    const currentPath = window.location.pathname
    const titleImg = {
        '/': 'static/imgs/home-logo.png'
    }

    $('.pageTitle img').attr('src', titleImg[currentPath])
}

includeHTML()
autoImportHeaderImg()

